import ViewComponent from "./components/ViewComponent.vue";

export default {
  name: "App",
  data() {
    return {
      targetDate: new Date("Wed Sep 21 2022 22:09:00"),
      interval: new Date("Wed Sep 21 2022 21:49:50") - new Date(),
    };
  },
  components: {
    ViewComponent: ViewComponent,
  },
  methods: {
    getInterval() {
      this.interval = this.targetDate - new Date();
    },
  },
  computed: {
    intervalValue() {
      return this.interval;
    },
  },
  mounted() {
    setInterval(this.getInterval, 30);
  },
};
</script>

