<template>
  <div
    class="container position-absolute top-50 start-50 translate-middle"
    :class="classObject"
  >
    <div class=""></div>
    <div class="col">
      <a class="col fs-3 fw-normal text-decoration-none text-dark mt-3 pt-3">
        사귄지 딱
      </a>
      <a class="col fs-3 fw-bold text-decoration-none text-dark"> 300일 </a>
      <a class="col fs-3 fw-normal text-decoration-none text-dark">
        되는 시간까지
      </a>
    </div>
    <div class="col">
      <div class="pt-3"></div>
      <div class="container p-3">
        <a
          class="fw-bold text-decoration-none text-dark ps-3"
          style="font-size: 6em"
          >{{ timer.days }}</a
        >
        <a class="fs-3 fw-bold text-decoration-none text-dark">일 </a>
        <a
          class="fw-bold text-decoration-none text-dark ps-3"
          style="font-size: 6em"
          >{{ timer.hour }}</a
        >
        <a class="fs-3 fw-bold text-decoration-none text-dark">시간 </a>
        <a
          class="fw-bold text-decoration-none text-dark ps-3"
          style="font-size: 6em"
          >{{ timer.minute }}</a
        >
        <a class="fs-3 fw-bold text-decoration-none text-dark">분 </a>
        <a
          class="fw-bold text-decoration-none text-dark ps-3"
          style="font-size: 6em"
          >{{ timer.second }}</a
        >
        <a class="fs-3 fw-bold text-decoration-none text-dark">초 </a>
      </div>
      <div>
        <a class="fs-4 fw-normal text-decoration-none text-dark">남았습니다!</a>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "CountDown",
  props: {
    interval: Number,
  },
  data() {
    return {
      fadeTiming: false,
      timer: {
        days: "00",
        hour: "00",
        minute: "00",
        second: "00",
      },
    };
  },
  methods: {
    getMilliseconds() {
      const divmod = (x, y) => {
        return [Math.floor(x / y), x % y];
      };
      const interval = this.interval;
      if (interval >= 0) {
        const [seconds] = divmod(interval, 1000);
        const [minutes, second] = divmod(seconds, 60);
        const [hours, minute] = divmod(minutes, 60);
        const [days, hour] = divmod(hours, 24);
        this.timer.days = days >= 10 ? days : "0" + days;
        this.timer.hour = hour >= 10 ? hour : "0" + hour;
        this.timer.minute = minute >= 10 ? minute : "0" + minute;
        this.timer.second = second >= 10 ? second : "0" + second;
      } else {
        this.fadeTiming = true;
      }
      console.log(this.interval);
    },
  },
  mounted() {
    this.loading = setInterval(this.getMilliseconds, 100);
  },
  computed: {
    classObject() {
      return {
        fadeoutbox: this.fadeTiming,
      };
    },
  },
};
</script>
<style lang="css">
.fadeoutbox {
  display: inline-block;
  animation: fadeout 1.5s;
  -moz-animation: fadeout 1.5s; /* Firefox */
  -webkit-animation: fadeout 1.5s; /* Safari and Chrome */
  -o-animation: fadeout 1.5s; /* Opera */
  animation-fill-mode: forwards;
}
@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-moz-keyframes fadeout {
  /* Firefox */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-webkit-keyframes fadeout {
  /* Safari and Chrome */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-o-keyframes fadeout {
  /* Opera */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
</style>
