<script>
import LetterTextOne from "./LetterTextOne.vue";
import LetterTextTwo from "./LetterTextTwo.vue";
import LetterTextThree from "./LetterTextThree.vue";
import LetterTextFour from "./LetterTextFour.vue";
import LetterTextFive from "./LetterTextFive.vue";
import LetterTextSix from "./LetterTextSix.vue";

export default {
  name: "EventLetter",
  components: {
    LetterTextOne,
    LetterTextTwo,
    LetterTextThree,
    LetterTextFour,
    LetterTextFive,
    LetterTextSix,
  },
  data() {
    return {
      opened: new Date(),
      titlebox: {
        moveTop: false,
      },
      letterPage: 1,
    };
  },
  methods: {
    nextPage() {
      this.letterPage += 1;
    },
    prevPage() {
      this.letterPage -= 1;
    },
  },
  computed: {
    titleObject() {
      return {
        moveTop: this.titlebox.moveTop,
      };
    },
    letterOne() {
      if (this.letterPage === 1) {
        return { fadeinboxLettertext: true, fadeoutbox: false };
      } else {
        return { fadeinboxLettertext: false, fadeoutbox: true };
      }
    },
    letterTwo() {
      if (this.letterPage === 2) {
        return { fadeinboxLettertext: true, fadeoutbox: false };
      } else {
        return { fadeinboxLettertext: false, fadeoutbox: true };
      }
    },
    letterThree() {
      if (this.letterPage === 3) {
        return { fadeinboxLettertext: true, fadeoutbox: false };
      } else {
        return { fadeinboxLettertext: false, fadeoutbox: true };
      }
    },
    letterFour() {
      if (this.letterPage === 4) {
        return { fadeinboxLettertext: true, fadeoutbox: false };
      } else {
        return { fadeinboxLettertext: false, fadeoutbox: true };
      }
    },
    letterFive() {
      if (this.letterPage === 5) {
        return { fadeinboxLettertext: true, fadeoutbox: false };
      } else {
        return { fadeinboxLettertext: false, fadeoutbox: true };
      }
    },
    letterSix() {
      if (this.letterPage === 6) {
        return { fadeinboxLettertextLast: true, fadeoutbox: false };
      } else {
        return { fadeinboxLettertextLast: false, fadeoutbox: true };
      }
    },
    blockPrev() {
      if (this.letterPage === 1) {
        return true;
      } else {
        return false;
      }
    },
    blockNext() {
      if (this.letterPage === 6) {
        return true;
      } else {
        return false;
      }
    },
  },
};
</script>

