<template>
  <div>
    <CountDown :interval="interval" :style="{ display: countDownDisplay }" />
    <EventLetter :style="{ display: eventLetterDisplay }" />
  </div>
</template>
<script>
import CountDown from "./CountDown.vue";
import EventLetter from "./EventLetter";
export default {
  name: "ViewComponent",
  components: {
    CountDown: CountDown,
    EventLetter: EventLetter,
  },
  data() {
    return {
      // targetDate: new Date("Wed Sep 23 2022 19:16:00"),
      targetDate: new Date(new Date().setSeconds(40)),
      interval: 0,
      countDownDisplay: "inline-block",
      eventLetterDisplay: "none",
    };
  },
  methods: {
    getInterval() {
      if (this.interval > -1501) {
        this.interval = this.targetDate - new Date();
      }
    },
    selectScene() {
      if (this.interval < -1500) {
        this.countDownDisplay = "none";
        this.eventLetterDisplay = "inline-block";
      }
    },
  },
  mounted() {
    setInterval(this.getInterval, 30);
    setInterval(this.selectScene, 30);
  },
};
</script>
<style lang="css"></style>
